import React, {useState, useEffect} from 'react';
import {Map, MapMarker} from 'react-kakao-maps-sdk';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import ToastMessage from '../components/common/ToastMessage.js';
import coupleIcon from '../static/img/icon/coupleCharacter.png';
import copyIcon from '../static/img/icon/copyIcon.png';
import kakaonavi from '../static/img/icon/kakaonavi.png';
import tmap from '../static/img/icon/tmap.png';
import navermap from '../static/img/icon/navermap.png';
import common from '../static/common/common.json';
import '../static/css/KakaoMap.css';
function KakaoMap() {
    const [ToastStatus, setToastStatus] = useState(false);
    const activeToast = () => {
        setToastStatus(true);
    };

    const runTmap = () => {
        window.location.href = 'tmap://search?name=';
    };

    const runNaverMap = () => {
        window.location.href = 'nmap://search?query=&appname=wedding-invite-app';
    };

    const rumKakaoNavi = () => {
        const kakao = window.Kakao;
        if (!kakao.isInitialized()) {
            kakao.init(common.KAKAO_JAVASCRIPT_KEY);
        }
        Kakao.Navi.start({
            name: ' ',
            x: 127.0995839,
            y: 37.51606,
            coordType: 'wgs84',
        });
    };

    useEffect(() => {
        if (ToastStatus) {
            setTimeout(() => setToastStatus(false), 1000);
        }
    }, [ToastStatus]);

    return (
        <div id="kakaoMapDiv">
            <p className="subTitle"></p>
            <Map
                id="kakaoMap"
                center={{lat: 37.5145, lng: 127.0995839}}
                style={{width: '100%', height: '180px'}}
                level={5}
                draggable={false}
                zoomable={false}
            >
                <MapMarker
                    position={{lat: 37.51606, lng: 127.0995839}}
                    image={{
                        src: coupleIcon,
                        size: {
                            width: 35,
                            height: 35,
                        },
                        options: {
                            offset: {x: 17, y: 47},
                        },
                    }}
                />
                <div className="navigationAppBtn">
                    <button onClick={runTmap}>
                        <img src={tmap} />
                    </button>
                    <button id="start-navigation" onClick={rumKakaoNavi}>
                        <img src={kakaonavi} />
                    </button>
                    <button onClick={runNaverMap}>
                        <img src={navermap} />
                    </button>
                </div>

                <div style={{textAlign: 'left', marginTop: '15px'}}>
                    <span style={{fontSize: '15px', fontWeight: 'bold'}}>
                         , 2 
                    </span>
                    <br />
                    <span style={{fontSize: '13px'}}>
                          35 137 (),
                        <br />
                         2&nbsp;&nbsp;
                        <CopyToClipboard
                            text="  35 137 "
                            onCopy={activeToast}
                        >
                            <span className="copyText">
                                <img className="prefixIcon2" src={copyIcon} />
                                &nbsp;
                            </span>
                        </CopyToClipboard>
                    </span>
                    <div>
                        <span style={{fontSize: '14px', fontWeight: 'bold'}}>{' '}</span>
                        <br />
                        <span style={{fontSize: '13px'}}>
                             7  200m , Tower730   ( 5)
                        </span>
                    </div>
                    <div>
                        <span style={{fontSize: '14px', fontWeight: 'bold'}}>{' '}</span>
                        <br />
                        <span style={{fontSize: '13px'}}>
                            -    200m  {'>'}   {' > '}
                            170m  {' > '}
                        </span>
                        <br />
                        <span style={{fontSize: '13px'}}>
                            -   {' > '} {' > '}
                              {' > '}170m  {' > '}
                            
                        </span>
                        <br />
                    </div>
                    <div>
                        <span style={{fontSize: '14px', fontWeight: 'bold'}}>{' '}</span>
                        <br />
                        <span style={{fontSize: '13px'}}>   2 </span>
                        <span className="warningMsg">
                            &nbsp;(B1  )
                            <br />
                            *   .
                            <br />
                            SUV       .
                        </span>
                    </div>
                    <div>
                        <span style={{fontSize: '14px', fontWeight: 'bold'}}>{' '}</span>
                        <br />
                        <span style={{fontSize: '13px'}}>
                            B1  (13:30 ~ 15:30) / *  14:00
                        </span>
                        <br />
                    </div>
                </div>
                <br />
                <br />
                <p>
                      <b style={{color: 'red', marginRight: '2px'}}></b>.
                    <br />
                       .
                </p>
            </Map>
            {ToastStatus && <ToastMessage msg=" ." />}
        </div>
    );
}

export default KakaoMap;
